import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { DropComponentData } from './drop-component-data.class';
import { IViewComponentDropData } from './interfaces/view-designer-canvas.interfaces';

@Injectable()
export class ViewDesignerCanvasService {
  private componentSelectSubject = new ReplaySubject<any>();
  private componentRemoveSubject = new ReplaySubject<string>();
  private componentDropSubject = new ReplaySubject<DropComponentData>();

  componentDrop$: Observable<DropComponentData> = this.componentDropSubject.asObservable();
  componentSelect$: Observable<any> = this.componentSelectSubject.asObservable();
  componentRemove$: Observable<string> = this.componentRemoveSubject.asObservable();

  selectComponent(guid: string): void {
    this.componentSelectSubject.next(guid);
  }

  removeComponent(guid: string): void {
    this.componentRemoveSubject.next(guid);
  }

  dropComponent(data: IViewComponentDropData, targetGuid: string, outletName: string, insertIndex: number): void {
    this.componentDropSubject.next(new DropComponentData(data, targetGuid, outletName, insertIndex));
  }
}
